.UserInfoWrraper {
    height: 100vh;
    padding-top: 10%;
}

.UserInfoWrraper ul {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
}

.userinfoli {
    width: 50%;
    height: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 0.3rem;
    margin-left: auto;
    margin-right: auto;
}